import React from 'react'
import Helmet from 'react-helmet'
import PropTypes from 'prop-types'
import Content from '../Content'
import PreFooter from "../PreFooter"

const AboutPageTemplate = ({ meta_title, meta_description, title, content, contentComponent }) => {
  const PageContent = contentComponent || Content

  return (
    <div className="bg-white flex flex-col">
      <div className="container mx-auto px-8">
        <Helmet>
          <title>{meta_title}</title>
          <meta name='description' content={meta_description} />
        </Helmet>

        <main className="flex flex-wrap items-center text-center md:text-left my-12 py-12 lg:px-24">
          <div className="w-full md:w-full whitespace-pre-line">
            <h1 className="text-5xl text-black leading-tight mb-6">
              {title}
            </h1>
          </div>
        </main>

        <h2 className='has-text-weight-semibold is-size-2'>
          {}
        </h2>
        <PageContent content={content} />

        <PreFooter />
      </div>
    </div>
  )
}

AboutPageTemplate.propTypes = {
  title: PropTypes.string.isRequired,
  content: PropTypes.string,
  contentComponent: PropTypes.func,
}

export default AboutPageTemplate
